.entry-content .wp-block-tasty-block-container {
  padding: 30px 50px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06); }

.wp-block-tastydigital-splash-box {
  position: relative; }
  .wp-block-tastydigital-splash-box > a {
    display: block;
    overflow: hidden;
    text-decoration: none; }
    .wp-block-tastydigital-splash-box > a img.splash-image {
      transform: scale(1);
      transform-origin: 50% 50%;
      transition: transform ease-in-out 0.2s;
      z-index: 0; }
    .wp-block-tastydigital-splash-box > a:hover img.splash-image {
      transform: scale(1.05); }
    .wp-block-tastydigital-splash-box > a .arrow-right {
      width: 0;
      height: 0;
      border-top: 9px solid transparent;
      border-bottom: 9px solid transparent;
      border-left: 16px solid #e4c220;
      position: absolute;
      right: 12px;
      top: 16px;
      transform: translateX(0);
      transition: transform 0.3s; }
    .wp-block-tastydigital-splash-box > a:hover .arrow-right {
      transform: translateX(3px); }
  .wp-block-tastydigital-splash-box .overlay {
    width: 100%;
    padding: 16px 40px 16px 18px;
    height: 52px;
    z-index: 2;
    position: relative; }
  .wp-block-tastydigital-splash-box h2 {
    margin: 0;
    color: #FFF;
    font-weight: 600;
    text-align: right;
    font-size: 18px; }
  .wp-block-tastydigital-splash-box .splash-logo {
    float: left;
    height: 19px; }
    .wp-block-tastydigital-splash-box .splash-logo img {
      height: 19px;
      width: auto; }
  .wp-block-tastydigital-splash-box .splash-holder {
    border-bottom: 7px solid #FFF;
    overflow: hidden; }
    .wp-block-tastydigital-splash-box .splash-holder img {
      margin-top: -5px; }
  .wp-block-tastydigital-splash-box.is-style-bordered {
    padding: 3px 3px 0; }
    .wp-block-tastydigital-splash-box.is-style-bordered .splash-holder {
      border: 7px solid #FFF; }
      .wp-block-tastydigital-splash-box.is-style-bordered .splash-holder img {
        margin-top: 0; }

.splash-image {
  text-align: center;
  background-color: #F5F5F5;
  min-width: 100%; }

.wp-block-tastydigital-splash-box-overlay {
  position: relative; }
  .wp-block-tastydigital-splash-box-overlay > a {
    display: block;
    overflow: hidden; }
    .wp-block-tastydigital-splash-box-overlay > a img.splash-image {
      transform: scale(1);
      transform-origin: 50% 50%;
      transition: transform ease-in-out 0.2s;
      z-index: 0; }
    .wp-block-tastydigital-splash-box-overlay > a:hover img.splash-image {
      transform: scale(1.05); }
  .wp-block-tastydigital-splash-box-overlay .overlay {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding: 20px;
    height: 88px;
    z-index: 2; }
  .wp-block-tastydigital-splash-box-overlay h2 {
    margin: 5px 0 0;
    color: #FFF;
    font-weight: 700; }

.opacity-90 {
  opacity: 0.9;
  z-index: 1; }

.splash-image {
  text-align: center;
  background-color: #F5F5F5;
  min-width: 100%; }

@media only screen and (min-width: 960px) {
  .wp-block-group.constrain-content > .wp-block-group__inner-container {
    max-width: 1080px;
    margin-left: auto;
    margin-right: auto; } }

@media screen and (max-width: 768px) {
  .mobile-hidden {
    display: none; } }

.is-style-flexible-row .wp-block-group__inner-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center; }
  .is-style-flexible-row .wp-block-group__inner-container > * {
    padding-left: 0.6em;
    padding-right: 0.6em; }

.is-style-mega-title {
  text-transform: uppercase;
  font-weight: 800;
  letter-spacing: -0.0481em; }

h1.is-style-mega-title {
  font-size: 144px; }

h2.is-style-mega-title {
  font-size: 104px; }

h3.is-style-mega-title {
  font-size: 64px; }

h4.is-style-mega-title {
  font-size: 44px; }

h5.is-style-mega-title {
  font-size: 24px; }

h6.is-style-mega-title {
  font-size: 14px; }

@media (max-width: 575.98px) {
  h1.is-style-mega-title {
    font-size: 18vw; }
  h2.is-style-mega-title {
    font-size: 18vw; }
  h3.is-style-mega-title {
    font-size: 12vw; }
  h4.is-style-mega-title {
    font-size: 8vw; } }
